<script>
    import {getYoutubeIdByUrl, getVideoData} from '../../scripts/youtube'
    import {mapActions} from 'vuex'

    export default {
        name: "Home",
        data: () => ({
            plans: null,
            show_popup: false,
            current_tariff: null,
            countries: [],
            video_url: '',
            video_id: null,
            video_data: null,
            name: '',
            country: [],
            quantity: null,
            price: null,
            total_price: null,
            err: null
        }),
        props: ['width', 'news'],
        watch: {
            video_url: async function () {
                this.video_id = getYoutubeIdByUrl(this.video_url)
                this.video_data = await getVideoData(this.video_id)
            }
        },
        created: function () {
            window.scrollTo(0,0)
            this.getPlans()
            const instance = this.axios.create();
            delete instance.defaults.headers.common['Authorization'];
            instance.get('https://restcountries.eu/rest/v2/all').then(res => {
                this.countries = res.data.map(country => {
                    return country.name
                })
            })
        },
        methods: {
            ...mapActions(['setAmount']),
            genImageUrl(article) {
                return `${process.env.VUE_APP_SERVER_URL}${article.image}`
            },
            formOpenHandler() {
                this.show_popup = !this.show_popup
                if (this.show_popup) document.body.style.overflow = 'hidden'
                else document.body.style.overflow = 'visible'
            },
            async getPlans() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/client/plans/`,
                    method: 'GET'
                })
                this.current_tariff = res.data[0].id
                this.plans = res.data
            },
            async getPrice() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/client/price/?plan=${this.current_tariff}&amount=${this.quantity}`,
                method: 'GET'
                })
                this.price = `${res.data.amount} * ${res.data.price}`
                this.total_price = res.data.total_price
            },
            submit() {
                const data = {
                    plan: this.current_tariff,
                    amount: this.quantity,
                    video: this.video_url,
                    traffic: this.video_url,
                    countries: this.country.join(';')
                }
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/client/orders/`, method: 'POST', data
                }).then(res => {
                    this.setAmount()
                    this.$router.push(`/orders`)
                }).catch(err => {
                    this.err = err
                })
            }
        }
    }
</script>

